import React, { Fragment } from "react";
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ListItemText from '@mui/material/ListItemText';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';

function Definition({ definitions }) {
    return (<>
        {
            definitions.map(el => {
                return (<Fragment key={el.definition.toString()}>
                    {!Boolean(el.example)
                        && <List 
                            sx={{  bgcolor: 'background.paper',p:2}}
                            component="nav"
                            aria-labelledby="nested-list-subheader">
                                  <Divider light />
                            <ListItemText primary={el.definition}  />
                        </List>
                    }
                 

 
                    {Boolean(el.example) 
                    &&  <Accordion >
                            <AccordionSummary expandIcon={<ExpandMoreIcon />} sx={{ bgcolor: 'background.paper',p:2}}>
                                <Typography>{el.definition}</Typography>
                            </AccordionSummary>
                        <AccordionDetails>
                            <Typography>
                                {el.example}
                            </Typography>
                        </AccordionDetails>

                    </Accordion>}
                </Fragment>
                )
            })
        }
    </>


    )
}
export default Definition;